import * as Yup from "yup";

export const contactSchema = Yup.object().shape({
    fullname: Yup.string()
        .required("      ."),
    photo: Yup.string()
        .url("    ")
        .required("   ."),
    mobile: Yup.string()
        .required("    .")
        .matches(/^(09)\d{9}$/, '   '),
    email: Yup.string()
        .email("   .")
        .required("    ."),
    job: Yup.string().nullable(),
    group: Yup.string()
        .required("   ."),
});



